import {
  Inject,
  Injectable,
  PLATFORM_ID
} from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
@Injectable({
  providedIn: 'root'
})
export class SemVideoService {

  constructor( @Inject(HttpClient) protected readonly http: HttpClient, @Inject(PLATFORM_ID) public platformId: Object) { }

  public fetchVideos(query: string): any {
    return this.http
      .get(`https://www.googleapis.com/youtube/v3/search?part=snippet&maxResults=5&q=${query}
        &type=video
        &key=AIzaSyCVYzrBm1lzl9iZtNArxlwRym1LnUdQpPs`);
  }
}
